import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { PageContentComponent } from './page-content/page-content.component';

const routes: Routes  = [
  { path: '', redirectTo: '/home', pathMatch: 'full'},
  { path: 'home', component: PageContentComponent, data: { section: 'Home'}},
  { path: 'about', component: PageContentComponent, data: { section: 'About'}},
  { path: 'portfolio', component: PageContentComponent, data: { section: 'Portfolio'}},
  { path: 'resume', component: PageContentComponent, data: { section: 'Resume'}}
];

@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}



